.contact-info-form {
    margin-block: 5em;
}

.gform_wrapper {

    .gform_button {
        @extend .btn;
        @extend .btn-secondary;
    }
    
    .gform_confirmation_message {
    
        background: $light;
        color: $primary;
        padding: 2em;
        margin: 2em;
        border-radius: 25px;
    }
    
    .gfield_label {
        color: $light;
        opacity: 0;
        z-index: 0;
        position: relative;
        bottom: -50px;
        transition: all 0.5s ease;
    
        .gfield_required {
            color: $light;
        }
    }

    .show_label .gfield_label {
        opacity: 1;
        bottom: 0;
    }
    

    .focus-label {
        opacity: 1;
        bottom:0;
    }
    
    .ginput_container{
        border: none;
        border-radius: 5px;
    
        input, textarea {
            border-radius: 5px;
            padding: 15px !important;
            background-color: #fff;
            border: none;

        }
    }
    
}

.gform_confirmation_message {
    font-size: 24px;
    text-align: center;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    margin-inline: auto;
    color: $light;
    align-items: center;

    img {
        border-radius: 50%;
        width: 150px;
        margin: 1em;
    }
    }

    

